<template>
  <div class="wrapper-registration">
    <div class="left-registration"></div>
    <div class="right-registration">
      <router-link class="services-other" to="/"
        ><img src="@/assets/img/arrow-round-back.svg" />
        <span></span></router-link
      >
      <br />
      <br />
      <br />
      <br />
      <div class="logo-reg">
        <router-link to="/"><img src="@/assets/img/logo.svg"/></router-link>
      </div>
      <h2></h2>
      <p class="reg-pre">
              
      </p>
      <form v-show="!next_step" @submit.prevent="nextStep">
        <div id="reg-step1">
          <div class="input-reg-area">
            <input
              v-model="formData.email"
              class="mail-form"
              type="email"
              placeholder="E-mail"
              required
            />
          </div>
          <div class="input-reg-area">
            <input
              v-model="formData.phone"
              class="phone"
              type="text"
              placeholder=""
              required
            />
          </div>
          <div class="input-reg-area">
            <input
              v-model="formData.password"
              type="password"
              placeholder=""
              required
            />
            <span>  6 </span>
            <a href="#"><i class="fas fa-eye"></i></a>
          </div>
          <div class="input-reg-area">
            <input
              v-model="formData.confirm_password"
              type="password"
              placeholder=" "
              required
            />
            <a href="#"><i class="fas fa-eye"></i></a>
          </div>
          <div class="input-reg-submit">
            <input id="more-reg" type="submit" value="" />
          </div>
        </div>
      </form>
      <form v-show="next_step" @submit.prevent="signUp">
        <div id="reg-step2">
          <div class="input-reg-area">
            <input
              v-model="formData.name"
              type="text"
              placeholder="  "
              required
            />
          </div>
          <div class="input-reg-area">
            <input
              v-model="formData.country"
              type="text"
              placeholder=""
              required
            />
          </div>
          <div class="input-reg-area">
            <input
              v-model="formData.city"
              type="text"
              placeholder=""
              required
            />
          </div>
          <div class="input-reg-area">
            <input
              v-model="formData.birthday"
              type="text"
              placeholder=" "
              required
            />
          </div>
          <div class="input-reg-submit">
            <input
              type="submit"
              value=""
              :disabled="!accept_terms"
            />
          </div>
        </div>
      </form>
      <p class="login-reg">
         ?
        <router-link to="/login">  </router-link>
      </p>
      <label v-show="next_step" class="checkbox-reg">
        <input v-model="accept_terms" type="checkbox" />
        <span
          >       
             </span
        >
      </label>
    </div>
  </div>
</template>

<script>
export default {
  name: "SignUp",
  data() {
    return {
      accept_terms: false,
      next_step: false,
      formData: {
        birthday: null,
        city: null,
        country: null,
        email: null,
        name: null,
        password: null,
        confirm_password: null,
        phone: null
      }
    };
  },
  methods: {
    nextStep() {
      this.next_step =
        this.formData.email &&
        this.formData.phone &&
        this.formData.password &&
        this.formData.password === this.formData.confirm_password;
    },
    signUp() {
      this.$auth.register({
        data: this.formData,
        success() {
          this.$router.push({ name: "login" });
        }
      });
    }
  }
};
</script>

